import React, { useRef, useState } from "react";
import styles from "./modal.module.css";
import closeIconURL from "./close.svg";

interface ModalProps {
  children: React.ReactNode;
  transparentContent?: Boolean;
  unavailableToClose?: Boolean;
  closeModal?: Function;
}

export default function Modal({
  children,
  closeModal = () => {},
  transparentContent = false,
  unavailableToClose = false,
}: ModalProps) {
  const closer = () => {
    closeModal();
  };
  return (
    <div className={styles.modal}>
      <div className={styles.backdrop} onClick={(e) => closer()}></div>
      <div
        className={
          styles.content +
          (transparentContent ? "" : " " + styles.contentWithBg)
        }
      >
        {!unavailableToClose ? (
          <button className={styles.closeButton} onClick={(e) => closer()}>
            <img src={closeIconURL} alt="" className={styles.closeButtonIcon} />
          </button>
        ) : (
          ""
        )}
        {children}
      </div>
    </div>
  );
}
